import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { CookiesService } from 'src/app/core/services/cookies/cookies.service';
import { EnvironmentService, AuthenticationService } from 'src/app/core/services';

@Injectable()
export class CategoriesService {
    

    constructor(
        public http: HttpClient,
        public envService: EnvironmentService,
        public cookies: CookiesService,
        public authentication: AuthenticationService
    ){}

    public getCategories(): any{
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl') +'/category/by-owner/'+ window['instance_id']).subscribe(resolve, reject);
        });  
    }

    public async getCategory(categoryid) {
        let categoryInfo, subcategories;
        await this.getCategoryInfo(categoryid).then((data)=>{ categoryInfo = data }, error => {
            console.log("unable to load category info");    
        });
        await this.getSubcategories(categoryid).then((data)=>{ subcategories = data }, error => {
            console.log("unable to load subcategories");    
        });
        return {"info": categoryInfo,"sub":subcategories};
    }

    public getCategoryInfo(categoryid):any {
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl')+'/'+ window['instance_id']+'/category/'+categoryid).subscribe(resolve, reject);
        });  
    }
    
    public getSubcategories(categoryid): any{
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl')+'/'+ window['instance_id']+'/category/'+categoryid+'/children').subscribe(resolve, reject);
        });  
    }

    public deleteCategory(categoryid):any {
        let authinfo = this.authentication._authinfo.get();
        const options = {
            headers: new HttpHeaders({'Content-Type': 'application/json'}),
            body: authinfo 
        };
        return new Promise((resolve,reject) => {
            this.http.delete(this.envService.read('apiUrl')+'/'+ window['instance_id']+'/category/'+categoryid,options).subscribe(resolve, reject);
        });  
    }

    public createCategory(data): any{
        let authinfo = this.authentication._authinfo.get();
        return new Promise((resolve,reject) => {
            this.http.post(this.envService.read('apiUrl')+'/'+ window['instance_id']+'/category',{"auth_info":authinfo,"category":data}).subscribe(resolve, reject);
        });  
    }

    
    public editCategory(categoryId,data): any{
        let authinfo = this.authentication._authinfo.get();
        return new Promise((resolve,reject) => {
            this.http.patch(this.envService.read('apiUrl')+'/'+ window['instance_id']+'/category/' +categoryId+ '/name' ,{"auth_info":authinfo,"string":data['name']}).subscribe(resolve, reject);
        });  
    }


}


